import { document } from 'ssr-window';
import Support from '../../utils/support';
export default {
  name: 'support',
  proto: {
    support: Support
  },
  static: {
    support: Support
  },
  on: {
    init: function init() {
      var html = document.querySelector('html');
      if (!html) return;
      var classNames = []; // Add html classes

      classNames.forEach(function (className) {
        html.classList.add(className);
      });
    }
  }
};